.header-color{
background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{

background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
top: 0;
left: 0;
width: 100%;

}

.avatar-img {
    height: 250px;
    padding: 10em;
    display: block;
    margin: 0 auto;
   
  }
  
  .banner-text {
    background-color: black;
    opacity: .8;
    width: 65%;
    margin: auto;
    border-radius: 8px;
    
  }
  
  .banner-text h1 {
    font-size: 60px;
    font-weight: bold;
    color: white;
   
  }

  .banner-text hr{
   border-top: 5px dotted white   ;
   width: 50%;
   margin: auto;
    

  }

  .banner-text p {
   color: white;
   font-size: 20px;
   padding: 1em;


  }

  .social-links{
   display: flex;
   justify-content: space-between;
   margin: auto;
   width: 50%;

  }

  .social-links i{
   color: white;
   font-size: 5em;
   padding: 8px;

  }


  .projects-grid {
    display: flex;
  }


  /* contact CSS */


  .contact-body{
   margin: auto;
    width: 100%;
   height: 100%;
   justify-content: center; 
   padding-top: 2em;
  }


  .contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 600px;
  background: white;
  color: black;
  margin-bottom: 20px;

  }

  .contact-grid h2{
   font-family: 'Anton', sans-serif;

  }

  .contact-grid p {
    font-family: 'Oxygen', sans-serif;
    font-weight: bold;

  }

  .contact-grid hr{
    border-top:  5px dotted black;
    width: 50%;
    margin: auto;

  }

  .contact-list i{

    font-size: 30px;
    padding-right: 4rem;
  }